import React from 'react';
import CardActionArea from '@material-ui/core/CardActionArea';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import {ReactComponent as PDFLogo} from './twotone-picture_as_pdf-24px.svg';

const styles = theme => ({
	resultCard:{
		margin:theme.spacing.unit,
		width:'70%',

	},
	title:{
		display:'flex',
		flexDirection:'row',
		height:'100%',
	}
})

class ResultCard extends React.Component{
	handleOnClick(){
		fetch('http://localhost:5000/getfile' , {
			headers:{
				'Access-Control-Allow-Origin' : '*',
				'Content-Type': 'application/json',
			},
			method: 'POST',
			body: JSON.stringify(this.props.filepath)
		})
		.then(res=>res.blob())
		.then(blob=>URL.createObjectURL(blob))
		.then(url=>{
			window.open(url,'_blank');
			URL.revokeObjectURL(url);
		})
	}
	render(){
		const {classes} = this.props;
		let paragraph = this.props.paragraph;
		paragraph = paragraph.replace(/(<em>)/g,'<span style="background:yellow;padding:5px">');
		paragraph = paragraph.replace(/(<\/em>)/g,'</span>')
		return(
			<Card className={classes.resultCard}>
				<CardActionArea  onClick={this.handleOnClick.bind(this)}>
					<CardContent className={classes.title}>
						<PDFLogo width={48} height={48}/>
						<Typography style={{paddingTop:3}} variant="h4" component='div'>
							{this.props.title}
						</Typography>
					</CardContent>
				</CardActionArea>
				<CardContent>
					<Typography>
						Score: {this.props.score}
					</Typography>
					<Typography component='div'>
						<div dangerouslySetInnerHTML={{__html: paragraph}}/>
					</Typography>
				</CardContent>
			</Card>
			)
	}

}

export default withStyles(styles)(ResultCard);