import React from 'react';
import PropTypes from 'prop-types';
import InputBase from '@material-ui/core/InputBase';
import { withStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';
import CFFlogo from './cff_logo.png';
import ResultList from './ResultList.js';
import ReactPaginate from 'react-paginate';
import Loader from 'react-loader-spinner';

const styles = theme => ({
  outsideContainer:{
    display:'flex',
    flexDirection:'column',
  },

  pagination:{
    display:'flex',
    padding:'20px',
    fontSize: '20px',
    
  },


  bullets:{
    cursor:'pointer',
    listStyleType: 'none',
    margin: theme.spacing.unit,

  },

  active:{
     textDecoration:'underline'
  },

  root: {
   width: '50%',
   display: 'flex',
   paddingTop: 10,
   paddingLeft: 15,
  },
  // title: {
  //   display: 'none',
  //   [theme.breakpoints.up('sm')]: {
  //     display: 'block',
  //   },
  logo: {
    margin: 30,
    margin: theme.spacing.unit,
    paddingLeft: 10,
    },
  //},
  form:{
    width:'100%',
    display:'flex'
  },
  button:{
    margin: 15 ,
    padding: 10,

  },

  search: {
    width: '100%',
    borderRadius: 10, 
    borderWidth: 10,
    padding: 10, 
    margin: 30,
    marginLeft: 20,
    margin: theme.spacing.unit,
    backgroundColor: '#eeeeee',

    },

  inputRoot: {
    color: 'inherit',
    width: '100%',
  },

  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    paddingLeft: theme.spacing.unit,
    transition: theme.transitions.create('width'),
    [theme.breakpoints.up('sm')]: {
      '&:focus': {
      },
    },
  },
});


class Resultpage extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      query:"",
      results:[],
      paginatedResults:[],
      gotResults:false,
      offset: 0,
    }
  }

  componentDidMount(){
    if (this.props.location.state !== undefined){
      // this.setState({results:this.props.location.state.results})
      // this.setState({paginatedResults:this.props.location.state.results.slice(this.state.offset,this.state.offset+5)})
      // this.setState({gotResults:true})
      // window.scrollTo(0,0)

      let data = {'query':this.props.location.state.query};
      
      fetch('http://localhost:5000/query' , {
      headers:{
        'Access-Control-Allow-Origin' : '*',
        'Content-Type' : 'application/json'
      },
      method: 'POST' ,
      body: JSON.stringify(data)
    }).then(res => res.json())
      .then(res=> {
            console.log(res.length)
            this.setState({results:res})
            this.setState({paginatedResults:res.slice(this.state.offset,this.state.offset+5)})
            }
        )
      .then(res=> this.setState({gotResults:true}))

    }
  }
  
  handleSubmit(e){
    this.setState({gotResults:false})
    let data = {'query':this.state.query};
    e.preventDefault()
    console.log("OKOKO")
    
    fetch('http://localhost:5000/query' , {
    headers:{
      'Access-Control-Allow-Origin' : '*',
      'Content-Type' : 'application/json'
    },
    method: 'POST' ,
    body: JSON.stringify(data)
  }).then(res => res.json())
    .then(res=> {
          console.log(res.length)
          this.setState({results:res})
          this.setState({paginatedResults:res.slice(this.state.offset,this.state.offset+5)})
          }
      )
    .then(res=> this.setState({gotResults:true}))

  }

  handleTextOnChange(e){
    this.setState({query:e.target.value})
    console.log(this.state.query);
  }

  handlePageClick = data => {
    let selected = data.selected;
    let offset = Math.ceil(selected * 5); //5 per page 

    this.setState({paginatedResults:this.state.results.slice(offset,offset+5)})
    window.scrollTo(0,0)
  }

  render(){
    const {classes} = this.props;

    return(
      <div className={classes.outsideContainer}>
        <div className={classes.root}>
          <div className={classes.logo}>
            <img
              src={CFFlogo} 
              alt="logo" width="100px"/>
          </div>
          <form onSubmit={this.handleSubmit.bind(this)} className={classes.form}>
            <div className={classes.search}>
              <InputBase
                onChange = {this.handleTextOnChange.bind(this)}
                placeholder="Search..."
                fullWidth
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                  }}
                />
              </div>
              <Button type="submit" variant="contained" className={classes.button}>
                Search
              </Button>
          </form>
        </div>
        <div style={{display:'flex' , flexDirection:'column' , alignItems:'center'}}>
        {
          this.state.gotResults? 
          ([
            <ResultList results={this.state.paginatedResults}/>,
            <ReactPaginate 
              previousLabel = {'Previous'}
              nextLabel = {'Next'}
              breakClassName={classes.bullets}
              pageCount={Math.ceil(this.state.results.length/5)}
              marginPagesDisplayed={2}
              pageRangeDisplayed={10}
              onPageChange={this.handlePageClick.bind(this)}
              containerClassName={classes.pagination}
              pageClassName={classes.bullets}
              previousClassName={classes.bullets}
              nextClassName={classes.bullets}
              activeClassName = {classes.active}
              activeLinkClassName={classes.active}
            />
          ])
          :
          ([
            <Loader type="Oval" color="#00BFFF"/>,
            <div>Fetching results...</div>

          ])

        }
        </div>

      </div>
    )
  }
}

Resultpage.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Resultpage);


